
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

$blue: #00539CFF;
$yellow: rgb(247, 215, 128);
$offwhite: #f8f8ff;
$text:  'Fauna One', serif;
$heading: 'Cinzel Decorative', cursive;


html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

body {
  background-color: $yellow;
  
  margin: auto;
}
a {
  text-decoration: none;
  color: inherit;
  display: inline-block;
  width: 100%;
}
/* NAV HEADER */

.header-nav {
  color: $offwhite;
  height: 100px;
  background-color: $blue;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-around;
  align-items: center;
  box-shadow: 2px 2px 2px lightgray;
  font-family: $text;
  

  div {
    width: 30%;
    
  }
}

.header-how-to {
  align-items: center;
  
  .how-to-link {
    cursor: pointer;
    text-align: center;
    margin: auto;
    &:hover{
      font-weight: 700;
    } 
  }
}

.header-title {

  .title{
    font-family: $heading;
    font-size: 40px;
    text-align: center;
  }
}

.header-about-list {

  ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
  }
  

  i {
    &:hover {
      color: $yellow;
      transition: 0.8s;
    }
  }
}

/* Canvas*/

.canvas-container {
  margin-top: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 500px;
}
.canvas {
  width: 800px;
  height: 100%;
  
  background-color: gray;
  
}

/* User Form*/

.user-interaction-form {
  width: 800px;
  margin: auto;
  padding-top: 15px;
  font-family: $text;
  display: flex;
  justify-content: space-around;
  align-items: center;

}
.start-button {
  padding: 10px 15px 10px 15px;
  background-color: $offwhite;
  font-family: $heading;
  font-size: 14px;
  border: 1px solid gray;
  border-radius: 20px;

  &:hover {
    background-color: $blue;
    transition: 0.8s;
  }
}

/* Modal */
.modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  visibility: hidden;
  opacity: 0;
  height: 100vh;
  width: 100vh;

    &.open {
      visibility: visible;
      opacity: 1;
    }

}

.modal-background {
  position: absolute;
  background: gray;
  opacity: 0.5;
  width: 100%;
  height: 100%;
}

.modal-container {
  width: 500px;
  height: 500px;
  background: white;
  position: relative;
  padding: 30px;
}

.modal-close {
  position: absolute;
  right: 10px;
  top: 10px;
  color: black;
  font-weight: bolder;
  cursor: pointer;
}



